import React from 'react';
import "./RadioButton.scss";

const RadioButton = (props) => {
  const labelRef = React.createRef()
  const spanClickHandler = () => {
    if (labelRef.current){
      labelRef.current.click()
    }
  }

  return (
    <div className={["radio d-flex mb-0 ", props.centerText ? "align-items-center" : "align-items-start"].join(" ")}>
      <input
        type="radio"
        name={props.name}
        id={props.id}
        checked={props.checked ? "checked" : ""}
        onChange={props.changeHandler}
      />
      <span className="radio-checkmark" onClick={spanClickHandler}/>
      <div className="d-flex flex-column ml-2 ">
        <label className={["mb-0", props.textClass ? props.textClass : ""].join(" ")}
               ref={labelRef}
               for={props.id}>{props.children}{props.display} {props.count ? "(" + props.count + ")" : null}</label>
        {props.subContent ?
          <div>
            {props.subContent}
          </div>
          : null}
      </div>
    </div>
  )
}

export default RadioButton;