import React from "react";
import './ProductDetailsModel.scss';
import GiftStatusPill from '../gift-status-pill/GiftStatusPill'
import {formatPrice, productImageLoadErrorHandler, productImageLoadHandler} from "../../../../utils/commonFunction";
import {CURRENCY} from "../../../../utils/constants";

const ProductDetailsModel = (props) => {
  return (
    <div className="row product-details-model">
      <div className="col-12 product-details-image">
        {props.data?.imageUrl ?
          <img className="product-img d-block mx-auto" src={props.data?.imageUrl}
               onError={(ev) => productImageLoadErrorHandler(ev)}
               onLoad={(ev) => productImageLoadHandler(ev)}
               alt={props.data?.title}/>
          :
          <div className="added-card-no-product-image"/>
        }
        {props.data.mostWanted ?
          <div
            className="d-flex align-items-center product-details-have product-details-must-have-btn px-1 added-gift-must-have-active">
            <i className="icon mr-1 d-block heart-fill"/>
            <p className="text-sm product-details-must-have-text">Must Have</p>
          </div>
          : <></>}
        <div className="px-3 mt-2">
          <p className="font-medium">{props.data.title}</p>
          {!props.data.item.isCashFund ?
            <a className="product-detail-link text-sm font-medium mt-1" href={props.data.item.storeItemUrl}
               target="_blank">View Details</a> : null}
          <div className="d-flex align-items-baseline give-gift-model">
            {props.data.isGroupGift ?
              <div className="guest-toggle-pills px-1 mr-1 d-flex align-items-center">
                <i className="guest-icon-active mr-1"/>
                <span className="text-sm guest-icon-text">Group Gift</span>
              </div> : null}
            {props.data.item.hideTotal ? null : <p className="mt-2">{formatPrice(props.data.item.price)}</p>}
          </div>
          {props.data.note ?
            <>
              <p className="mt-2 text-sm product-note font-medium">Note:</p>
              <p className="text-sm product-note">{props.data.note}</p>
            </>
            : null}
          <div className="d-flex mt-2 mb-3 flex-wrap align-items-end">
            {props.notFromGuestView ?
              // When used in Track Gift
              !props.data.isGroupGift ?
                !props.data.isCashAcceptedOnly ?
                  <>
                    {props.data.quantityStatus.purchased_quantity > 0 ?
                      <GiftStatusPill variant="purchased"
                                      text={`${props.data.purchasedQuantity} Purchased`}
                                      className="d-inline-block mr-1 mt-2"/>
                      : null}
                    {props.data.quantityStatus.reserved_quantity > 0 ?
                      <GiftStatusPill variant="reserved"
                                      text={`${props.data.quantityStatus.reserved_quantity} Reserved`}
                                      className="d-inline-block mr-1 mt-2"/>
                      : null}
                    {props.data.quantityStatus.still_needed > 0 && !props.data.isPurchased ?
                      <GiftStatusPill
                        text={`${props.data.quantityStatus.still_needed} ${props.data.quantityStatus.still_needed === props.data.quantity ? 'Requested' : 'Still Needed'}`}
                        className="d-inline-block mr-1 mt-2"/>
                      : null}
                  </>
                  :
                  <>
                    {(props.data.item.price * props.data.quantityStatus.buyable_quantity) > 0 ?
                      <GiftStatusPill variant="purchased"
                                      text={`${formatPrice(props.data.item.price * props.data.quantityStatus.buyable_quantity)} ${props.data.purchasedQuantity - props.data.alreadyBoughtProductsQuantity > 0 ? 'Remaining' : 'Received'}`}
                                      className="d-inline-block mr-1 mt-2"/>
                      : null}
                    {props.data.purchasedQuantity > 0 ?
                      <GiftStatusPill variant="purchased"
                                      text={`${props.data.purchasedQuantity} Purchased`}
                                      className="d-inline-block mr-1 mt-2"/>
                      : null}
                    {props.data.quantityStatus.still_needed > 0 ?
                      <GiftStatusPill
                        text={`${props.data.quantityStatus.still_needed} ${props.data.quantityStatus.still_needed === props.data.quantity ? 'Requested' : 'Still Needed'}`}
                        className="d-inline-block mr-1 mt-2"/>
                      : null}
                  </>
                :
                <div className="d-flex w-100 align-items-center flex-wrap">
                  {props.data.quantityStatus && props.data.quantityStatus.total_contribution_amount <= 0 ?
                    <GiftStatusPill
                      text={`${formatPrice(props.data.item.price * props.data.quantity, CURRENCY)} Requested`}
                      className="d-inline-block mr-1 mt-2"/>
                    :
                    props.data.quantity <= props.data.quantityStatus.purchased_quantity || (Number(props.data.registryCategories[0].category.categoryType.id) === 2 && props.data.isPurchased) ?
                      props.data.quantityStatus.total_contribution_amount - (props.data.item.price * props.data.alreadyBoughtProductsQuantity) - (props.data.item.price * (props.data.purchasedQuantity - props.data.alreadyBoughtProductsQuantity)) > 0 ?
                        <GiftStatusPill variant="purchased"
                          // text="Purchased"
                                        text={`${formatPrice(props.data.quantityStatus.total_contribution_amount - (props.data.item.price * props.data.alreadyBoughtProductsQuantity) - (props.data.item.price * (props.data.purchasedQuantity - props.data.alreadyBoughtProductsQuantity)), CURRENCY)} ${props.data.purchasedQuantity - props.data.alreadyBoughtProductsQuantity > 0 ? 'Remaining' : 'Received'}`}
                                        className="d-inline-block mr-1 mt-2"/>
                        : null
                      :
                      <div className="flex-grow-1 mr-1">
                          <span
                            className="text-sm font-medium">{`Received ${formatPrice(props.data.quantityStatus.total_contribution_amount - (props.data.item.price * props.data.alreadyBoughtProductsQuantity), CURRENCY)}`}
                          </span>
                        <span
                          className="text-sm">{` of ${formatPrice(props.data.item.price * (props.data.quantity - props.data.alreadyBoughtProductsQuantity), CURRENCY)}`}
                          </span>
                        <div className="added-card-group-gift-progress-bar w-100">
                          <div className="added-card-group-gift-progress mw-100"
                               style={{width: `${((props.data.quantityStatus.total_contribution_amount - (props.data.item.price * props.data.alreadyBoughtProductsQuantity)) / (props.data.item.price * props.data.quantity)) * 100}%`}}/>
                        </div>
                      </div>
                  }
                  {props.data.purchasedQuantity > 0 && Number(props.data.registryCategories[0].category.categoryType.id) !== 2 ?
                    <GiftStatusPill variant="purchased"
                                    text={`${props.data.purchasedQuantity} Purchased`}
                                    className={["d-inline-block mr-1", props.data.quantity <= props.data?.quantityStatus?.purchased_quantity ? "mt-2" : ""].join(" ")}/>
                    : null}
                </div>

              :
              // When used in Guest View
              <>
                {props.data.quantityStatus.purchased_quantity ?
                  <div>
                    <GiftStatusPill className="mr-1" variant="purchased"
                                    text={props.data.quantityStatus.purchased_quantity + " Purchased"}/>
                  </div>
                  : null}
                {props.data.quantityStatus.reserved_quantity ?
                  <div className="mr-1 mt-md-1 mt-lg-0">
                    <GiftStatusPill variant="reserved"
                                    text={props.data.quantityStatus.reserved_quantity + " Reserved"}/>
                  </div>
                  : null}
                {props.data.quantityStatus.still_needed > 0 && props.data.registryCategories && props.data.registryCategories.length > 0 && Number(props.data.registryCategories[0].category.categoryType.id) !== 2 ?
                  <div className="mr-1 mt-md-1 mt-lg-1">
                    <GiftStatusPill variant="default"
                                    text={`${props.data.quantityStatus.still_needed} ${props.data.quantityStatus.purchased_quantity === 0 ? " Requested" : " Still Needed"}`}/>
                  </div>
                  : null}
              </>
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProductDetailsModel;