import React from "react";
import "./Unsubscribe.scss";
import {connect} from "react-redux";
import Button from "../commons/components/button-component/ButtonComponent";
import {Redirect} from "react-router-dom";
import {parseUrlToken} from "../../utils/commonFunction";
import {unsubscribe} from "../../api/axios";
import {setLoader} from "../registry/RegistryActions";
import {toast} from 'react-toastify';
import Toast from "../commons/components/toast/Toast";

class Unsubscribe extends React.Component {

  state = {
    token: null
  }

  componentDidMount() {
    let params = new URLSearchParams(this.props.location.search)
    if (params.has("t")) {
      let token = parseUrlToken(params.get("t"))
      if (token) {
        this.setState({
          token: token
        })
      }
    }
  }

  unsubscribeClickHandler = () => {
    let textAreaEl = document.getElementById("unsubscribeReason");

    if (textAreaEl) {
      let reqData = {
        token: this.state.token,
        unsubscribed_reason: textAreaEl.value
      }

      this.props.dispatch(setLoader(true))
      unsubscribe(reqData).then(res => {
        if (res.data.message) {
          toast.success(res.data.message)
          textAreaEl.value = "";
        }
      }).catch(err => {
        if (err.response?.data?.error){
          toast.error(err.response.data.error)
        }
      }).finally((e) => {
        this.props.dispatch(setLoader(false))
      })
    }
  }

  render() {
    let params = new URLSearchParams(this.props.location.search)
    if (!params.has("t")) {
      window.open("/", "_self")
      return null;
    } else {
      return (
        <>
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-12 col-md-5">
                <div className="unsubscribe-main-content p-4 mt-5">
                  <div className="row">
                    <div className="col text-center">
                      <h1>Unsubscribe</h1>
                    </div>
                  </div>

                  <div className="row mt-4">
                    <div className="col">
                      <p className="text-sm">Please tell us why are you unsubscribing?</p>
                      <textarea rows={3}
                                id="unsubscribeReason"
                                className="w-100 p-1 mt-1 mb-0 text-body m-a-resize-textarea unsubscribe-input"/>
                    </div>
                  </div>

                  <div className="row mt-4">
                    <div className="col text-center">
                      <Button
                        variant="primary"
                        className="px-3 py-1 font-medium text-body"
                        onClick={this.unsubscribeClickHandler}
                      >Unsubscribe</Button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Toast/>
        </>
      )
    }

  }
}

export default connect()(Unsubscribe);